import React from 'react'
import styles from '../styles/About.module.css'
const AboutSection = () => {
  return (
    <div className={styles.aboutSection}>
      <h2>About Me</h2>
      <div className={styles.childAboutSection}>
        <div>
          <img src="https://avatars.githubusercontent.com/u/107463708?v=4" alt="profile pic" />
        </div>
        <div>
          <h3>
            A passionate web developer who has more than 1000+ hours of practical knowledge in coding and is proficient in  Node JS, React JS, JavaScript, CSS, HTML and GitHub. Looking to join an organization where he can show his skills and contribute to the organization.
          </h3>
        </div>
      </div>
    </div>
  )
}

export default AboutSection